// ** React Imports
import { Fragment, useState, useEffect } from 'react'
import UiLoader from '@components/ui-loader'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
// ** Invoice List Sidebar

// import AddQuiz from '../add'

// ** Store & Actions
import { useDispatch, useSelector } from 'react-redux'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Filter, HelpCircle } from 'react-feather'
import DataTable from 'react-data-table-component'
import { useForm, Controller } from 'react-hook-form'
import classnames from 'classnames'
import {
  Card,
  //Input,
  Row,
  Col,
  FormGroup,
  Label,
  Spinner,
  Button,
  Popover,
  PopoverHeader,
  PopoverBody
} from 'reactstrap'
import AppCollapse from '@components/app-collapse'
// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import {
  // getContestsListCSV,
  getfootballcontestdata,
  getfootballcontestdownload,
  contestfrom,
  contestto,
  quizfrom,
  quizto,
  winnerfrom,
  winnerto,
  clearData,
  getdaterange
} from '../../store/actions'
  
// ** Table Header

const ContestsList = () => {
  
  const CustomHeader = ({
    //handleFilter,
    //searchTerm,
    currentPage,
    setCurrentPage
  }) => {
    const defaultValues = {
      reactSelect: null
    }
    const { handleSubmit, control } = useForm({ defaultValues })
    const [val, setVal] = useState(false)
    const [popoverOpen, setPopoverOpen] = useState(false)
  const store = useSelector((state) => state.reports)
  const [picker, setPicker] = useState(store.contest_fot_from)
  const [dateto, setDateto] = useState(store.contest_fot_to)
const dispatch = useDispatch()
const edittime = (date) => {
  setVal(false)
  const local = new Date(date)
  const datestring =  `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
  setPicker(datestring)
  
}

const edit = (date) => {
  setVal(false)  
  const local = new Date(date)
  const datestring =  `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
  setDateto(datestring)
}

const dateConverter = (startDate, timeEnd) => {
  const newStartDate = new Date(startDate)
  const newEndDate = new Date(timeEnd)
  const one_day = 1000 * 60 * 60 * 24
  let result = ''
  result = Math.ceil((newEndDate.getTime() - newStartDate.getTime()) / (one_day))
  console.log('date Converter result', result)
  if (result < 0) { return 0 }
  dispatch(getdaterange(result))
  return result
}

const onSubmit = (e) => {
  e.preventDefault()
  setVal(true)
  if ((picker === '' || picker === 'NaN-NaN-NaN')  || (dateto === '' || dateto === 'NaN-NaN-NaN')) {
    //
  } else {
    setCurrentPage(1)
    dispatch(getfootballcontestdata(currentPage, picker, dateto))
    dispatch(contestfrom(picker))
    dispatch(contestto(dateto))  
    dateConverter(picker, dateto)  
  }
}
console.log(store.contest_fot_from)
console.log(store.contest_fot_to)
//curentpage
const download = () => {
  dispatch(getfootballcontestdownload(picker, dateto))
}

const data = [
  {
    title: <div><Filter size={18}/> Filter...</div>,
    content: (
      <>
      <Row className='mx-auto'>
        <Col  className="d-flex align-items-center p-0">
        <div className="d-flex align-items-center w-100">
          
          <Label className="mb-0">From:</Label>&nbsp;&nbsp;
        
                 <Controller
              // as={Select}
              control={control}
              render={({ field: { ...field } }) => (
                <Flatpickr
                  {...field}
                  value={store.contest_fot_from}
                  placeholder='select from date'
                  onChange={date => edittime(date)}        
                  options = {{
                    // defaultDate : '01-05-1999',
                    dateFormat: "Y-m-d"
                }}
                id='range-picker'
                  // theme={selectThemeColors}
                  className={classnames('form-control', { 'is-invalid':  (picker === '' && val) || (picker === 'NaN-NaN-NaN' && val) })}
                  // classNamePrefix='select'
                  // isClearable={true}
                  />
                  )}
                  name='reactSelect'
              // defaultValue={series[0]}
            />
       &emsp;&nbsp;
          
          <Label className="mb-0">To:</Label>&nbsp;&nbsp;   
          
                <Controller
              // as={Select}
              control={control}
              render={({ field: { ...field } }) => (
                <Flatpickr
                  {...field}
                  value={store.contest_fot_to}
                  placeholder='select to date'
                  onChange={dat => edit(dat)}   
                  options = {{
                    // defaultDate : '01-05-1999',
                    dateFormat: "Y-m-d"
                }}
                id='range-picker'
                  // theme={selectThemeColors}
                  className={classnames('form-control', { 'is-invalid':  (dateto === '' && val) || (dateto === 'NaN-NaN-NaN' && val) })}
                
                  />
                  )}
                  name='reactSelect'
              // defaultValue={series[0]}
            />
           &emsp;&nbsp; 
        </div>
      </Col>
      </Row>
      {(picker === '' && val) || (picker === 'NaN-NaN-NaN' && val) || (dateto === '' && val) || (dateto === 'NaN-NaN-NaN' && val) ?  <h5 className='text-danger ml-3'>Please select a date</h5> : ""}
      <Row className='ml-1 mt-1'>
        <FormGroup className="d-flex mb-0">
              <Button.Ripple
                className="mx-2"
                color="primary"
                type="submit"
                onClick={(e) => handleSubmit(onSubmit(e))}
              >
                Submit
              </Button.Ripple>
        </FormGroup> 
      </Row>
      </>
    )
  }
]

return (
  <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
    <AppCollapse data={data} type='border' accordion className='mb-1' />
      <Row>
        <Col
          xl="6"
          className="ml-auto d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
        >
          <div className="d-flex align-items-center mb-sm-0 mb-1 mr-1">
         
          </div>
          <Button.Ripple 
                color='primary'
                disabled ={store.date_range > 90}
                onClick ={() => download()}>      
                {store.download_timer ?  
                <>
                <Spinner color='white' size='sm' />
                <span className='ml-50'>Loading...</span>
                </> : 'Download CSV' }
      </Button.Ripple>
      <Button.Ripple
                outline id='controlledPopover'
                color='flat-info'
                >
                  <HelpCircle />
                </Button.Ripple>
      <Popover
        placement='top'
        target='controlledPopover'
        isOpen={popoverOpen}
        toggle={() => setPopoverOpen(!popoverOpen)}
      >
        <PopoverHeader>INFO</PopoverHeader>
        <PopoverBody>
                  pick a date less than 90 days to download
        </PopoverBody>
      </Popover>
        </Col>
      </Row>
      <div>      
        </div>
    </div>
  )
}

  // ** Store Vars

  const store = useSelector((state) => state.reports)
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const dispatch = useDispatch()
  
 
  console.log('pagination', store.football_perpage)
 
  useEffect(() => {
    if (store.isloading) {
      <Spinner/>
    } else {
      setListData(store.football_contest)
    }
  }, [store.isloading])
  console.log(store.contest_fot_from)
  console.log(store.contest_fot_to)
  useEffect(() => {
    dispatch(getfootballcontestdata(currentPage, store.contest_fot_from, store.contest_fot_to))
    dispatch(winnerfrom("")) 
    dispatch(winnerto(""))
    dispatch(quizfrom(""))
    dispatch(quizto(""))
  }, [currentPage])

  // ** User filter options

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = (e) => {
    // const value = parseInt(e.currentTarget.value)
    console.log(e)
    // setRowsPerPage(store.football_contest)
    setCurrentPage(1)
  }
                  
  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
    if (searchTerm) {
      const filterList = store.football_contest.filter((userItem) => {

        if (userItem.contest_name) {
          return userItem.contest_name.toLowerCase().includes(searchTerm.toLowerCase())
        }
      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.football_contest)
    }
  }, [searchTerm])

  useEffect(() => {
    return () => {
      dispatch(clearData())
    }
  }, [])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    // const count = Number(Math.ceil(listData.length / rowsPerPage))
    const count = store.football_contest_perpage.last_page
      console.log(count)
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    // if (store.allContests.length > 0) {
    //   return store.allContests
    // } else
    if (listData.length === 0 && isFiltered) {
      return []
    } else {
      return listData
    }
  }

  const columns = [
    {
      name: '#',
      minWidth: '20px',
      sortable: true,
      cell: (row, pos) => `#${store.football_contest_perpage.from + pos}`
    },
    {
      name: 'Match',
      minWidth: '250px',
      selector: 'fixture.fixture_name',
      sortable: true,
      cell: (row) => (row.fixture?.fixture_name ? row.fixture?.fixture_name : '-')
    },
    {
      name: 'Contest Category',
      minWidth: '160px',
      selector: 'contests.contest_type',
      sortable: true,
      cell: (row) => row?.contests?.contest_type
    },
    {
      name: 'Total User Joined',
      minWidth: '160px',
      selector: 'contests.joined',
      sortable: true,
      cell: (row) => row?.contests?.joined
    },
    {
      name: 'Min',
      minWidth: '160px',
      selector: 'contests.size',
      sortable: true,
      cell: (row) => row?.contests?.min
    },
    {
      name: 'Max',
      minWidth: '160px',
      selector: 'contests.size',
      sortable: true,
      cell: (row) => row?.contests?.size 
    },
    {
      name: 'Entry Fees',
      minWidth: '160px',
      selector: 'contests.entry_fee',
      sortable: true,
      cell: (row) => row?.contests?.entry_fee 
    },
    {
      name: 'Total Team Created',
      minWidth: '160px',
      selector: 'userTeams',
      sortable: true,
      cell: (row) => (row.userTeams ? row?.userTeams : '-')
    },
    {
      name: 'Total Coin Distributed',
      minWidth: '160px',
      selector: 'winning',
      sortable: true,
      cell: (row) =>  (row.winning ? row?.winning : '-')
    }
  ]  

  return (
    <Fragment>
      <Card>
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage={handlePerPage}
              rowsPerPage
              searchTerm={searchTerm}
              handleFilter={handleFilter}
              current={currentPage}
              setCurrentPage={setCurrentPage}
            />
          }
        />
      </Card>
    </Fragment>
  )
}

export default ContestsList
